.todo {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.todo__input {
    width: 25%;
    padding: 10px 20px;
    margin: 10px 1px 28px 1px;
    border: 1px solid var(--background);
    border-radius: 20px;
    display: inline-block;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    font-family: Roboto;
    font-size: 16px;
}

.todo__input:focus {
    outline: none;
}

@media (max-width: 700px) {
    .todo {
        justify-content: right;
        margin-left: 120px;
    }

    .todo__input {
        width: 45%;
        margin-bottom: 26px;
    }
}